$accent-main: #95e1d3;
$accent-sec: #f38181;
$social-color: #fce38a;

@define-mixin heading-cursive {
	font-family: 'Kaushan Script';
	font-size: 24px;
	color: #333333;
	margin-bottom: 10px;
	display: block;
}
@define-mixin heading {
	font-family: 'Montserrat';
	font-size: 30px;
	font-weight: 700;
	color: #333;
	text-transform: uppercase;
	position: relative;
	margin-bottom: 80px;
	&::after {
		content: "";
		position:absolute;
		left: 50%;
		top: calc(100% + 35px);
		margin-left: -30px;
		width: 60px;
		height: 3px;
		display: block;
		background-color: $accent-sec;
	}
}
@define-mixin roboto-text {
	font-family: 'Roboto';
	font-size: 15px;
	line-height: 24px;
	color: #999999;
}
@define-mixin gradient {
	background-image: linear-gradient( to top, rgb(252,227,138) 0%, rgb(243,129,129) 100%);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html, body {
	width: 100%;
	height: 100%;
	font-family: 'Montserrat', sans-serif;
}
a {
	text-decoration: none;
	color: inherit;
}
ul {
	list-style: none;
}
figure img {
	width: 100%;
}
.wrapper {
	width: 1200px;
	margin: 0 auto;
	height: 100%;
	@media screen and (max-width: 1200px) {
		width: 100%;
		padding: 0 15px;
	}
}
.header {
	height: 95vh;
	position: relative;
	color: #fff;
	overflow: hidden;
}
.header_slider {
	position: relative;
	&::after {
		position: absolute;
		content:"";
		display: block;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		@mixin gradient;
		opacity: .9;
	}
	figure {
		height: 95vh;
		width: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		display: block;
		background-position: 50% 50%;
	}
}
.nav_wrap {
	display: flex;
	align-items: baseline;
	margin: 0 -20px;
	& > a {
		margin: 0 20px;
	}
}
.icons_wrap {
	a {
		margin: 0 15px;
		vertical-align: bottom;
	}
	@media screen and (max-width:768px) {
		position: absolute;
		top: 20px;
		right: 20px;
	}
	@media all and (max-width:480px) {
		right: 10px;
	}
	@media all and (max-width:400px) {
		right: 5px;
		a {
			margin: 0 10px;
		}
	}
}
.icon_cart path, .icon_search path {
	transition: .4s;
}
.icon_cart:hover path, .icon_search:hover path {
	@media screen and (min-width: 1025px) {
		fill: $social-color;
	}
}
.sup_heading {
	@media all and (max-width: 768px) {
		display: block;
		text-align: center;
	}
}
.sup_heading, .navigation ul{
	display: flex;
	align-items: baseline;
}
.sup_heading {
	justify-content: space-between;
}
.navigation {
	text-transform: uppercase;
	ul {
		text-align: right;

		li {
		padding: 0 25px;
		font-size: 14px;
		a {
			position: relative;
			transition: .4s;
			z-index: 10;
			&:hover {
				@media screen and (min-width: 1025px) {
					color: $social-color;
					&::after {
						opacity: 1;
						width: 100%;
					}
				}
			}
			&::after {
				content: "";
				width: 0;
				height: 3px;
				background: $social-color;
				position: absolute;
				top: calc(100% + 10px);
				left: 0;
				opacity: 0;
				transition: .4s ;
			}
		}
		}
	}
}
.logo {
	font-size: 30px;
	font-weight: 700;
	padding-top: 20px;
	display: block;
}
.heading {
	width: 100%;
	text-align: center;
	text-transform: uppercase;
	font-weight: 700;
	margin-top: 40vh;
	transform: translateY(-50%);
	h2 {
		font-size: 150px;
		@media all and (max-width: 992px) {
			font-size: 120px;
		}
		@media all and (max-width: 700px) {
			font-size: 100px;
		}
		@media all and (max-width: 600px) {
			font-size: 80px;
			line-height: 1em;
		}
		@media all and (max-width: 480px) {
			font-size: 60px;
		}
		@media all and (max-width: 350px) {
			font-size: 48px;
		}
	}
	span {
		font-family: 'Kaushan Script', cursive;
		text-transform: capitalize;
		font-size: 72px;
		line-height: normal;
		margin-bottom: 1.8vh;
		display: block;
		@media all and (max-width: 600px) {
			font-size: 50px;
		}
		@media all and (max-width: 480px) {
			font-size: 42px;
		}
		@media all and (max-width: 350px) {
			font-size: 36px;
		}
	}
}
.header_slider {
	height: 95vh;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: -1;
	overflow: hidden;
}
.heading_item {
	max-width: 850px;
	margin: 0 auto;

	h2 {
		line-height: 140px;
		position: relative;
		margin-bottom: 10.8vh;
		&::after {
			position: absolute;
			content: "";
			width: 60px;
			height: 3px;
			background-color: #fff;
			top: 100%;
			left: 50%;
			margin-left: -30px;
			margin-top: 5.4vh;
		}
	}
}
.button {
	font-size: 14px;
	width: 160px;
	border: 3px solid #fff;
	margin: 0 auto;
	position: relative;
	overflow: hidden;
	transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);

	a {
		display: block;
		line-height: 34px;
	}
}
.button::after {
	background: #fff;
  content: "";
  height: 155px;
  left: -75px;
  opacity: .2;
  position: absolute;
  top: -50px;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
  -webkit-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  width: 50px;
}
.button:hover::after {
	@media screen and (min-width: 993px) {
		left: 120%;
	  -webkit-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
	  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
	}
}
.pagging {
	ul {
		display: flex;
		justify-content: space-between;
		font-size: 18px;
		text-transform: uppercase;
		margin: 0 -15px;

		span {
			font-weight: 700;
			font-size: 24px;
		}

		li {
			margin: 0 15px;
			line-height: 70px;
			width: 100%;

			.timer {
				width: 100%;
				height: 3px;
				background-color: #fff;
				position: relative;
				.inProgress {
					position: absolute;
					content: "";
					width: 25%;
					height: 100%;
					background-color: #f38181;
					top: 0;
					left: 0;
				}
			}
		}
	}
}
.header_inner {
	height: calc(100% - 73px);
}

/*@import "parts/burger-menu";*/
.burger {
	position: absolute;
	display: none;
	left: 25px;
	top: 20px;
  border: 0;
  background: none;
  outline: 0;
  padding: 0;
  cursor: pointer;
  border-bottom: 4px solid currentColor;
  width: 28px;
  transition: border-bottom 1s ease-in-out;
  -webkit-transition: border-bottom 1s ease-in-out;
	z-index: 101;
}
.burger::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.burger:before {
  content: "";
  display: block;
  border-bottom: 4px solid currentColor;
  width: 100%;
  margin-bottom: 5px;
  transition: transform 0.5s ease-in-out;
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
}
.burger:after {
  content: "";
  display: block;
  border-bottom: 4px solid currentColor;
  width: 100%;
  margin-bottom: 5px;
  transition: transform 0.5s ease-in-out;
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
}
.burger.open {
  border-bottom: 4px solid transparent;
  transition: border-bottom 0.8s ease-in-out;
  -webkit-transition: border-bottom 0.8s ease-in-out;
}
.burger.open:before {
  transform: rotate(-405deg) translateY(1px) translateX(-3px);
  -webkit-transform: rotate(-405deg) translateY(1px) translateX(-3px);
  transition: transform 0.5s ease-in-out;
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
}
.burger.open:after {
  transform: rotate(405deg) translateY(-4px) translateX(-5px);
  -webkit-transform: rotate(405deg) translateY(-4px) translateX(-5px);
  transition: transform 0.5s ease-in-out;
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
}
.burger {
  color: white;
}
@media all and (max-width: 768px) {
	.burger {
		display: block;
	}
	.navigation ul {
		position: absolute;
		display: block;
		left: -40%;
		top: 50px;
		height: 100vh;
		max-width: 250px;
		transition: left .5s ease-in-out;
		text-align: left;
	}
	.navigation li a::before {
		top: 0;
		left: -4px;
		border-top: none;
		border-right: 4px solid #cfc91d;
		background-color: rgba(255,255,255,.1);
		width: 0;

	}
	.navigation li a:hover::before {
		width: 100%;
	}
	.navigation ul.show {
		left: 0;
	}
	.navigation ul li {
		display: block;
		margin: 15px 0;
	}
	.overlay {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0,0,0,.7);
		z-index: 9;
		display: none;
	}
}
